// Boucle @each pour générer des classes pour chaque couleur
@each $color, $value in (
  "tomato": $tomato,
  "darkslategrey": $darkslategrey,
  "charcoalgrey": $charcoalgrey,
  "grey": $grey,
  "white": $white
) {
  .bg-#{$color} {
    background-color: $value !important;
  }

  .text-#{$color} {
    color: $value !important;
  }
}

